import React from 'react';
import { InfoCard } from '@backstage/core-components';
import { Box, Typography } from '@material-ui/core';

export const GitHubStatus: React.FC = () => {
  const [statusData, setStatusData] = React.useState<any | null>(null);

  React.useEffect(() => {
    fetch('https://kctbh9vrtdwd.statuspage.io/api/v2/status.json')
      .then(response => response.json())
      .then(data => {
        const { status, page } = data;
        console.log(data);
        setStatusData({
          status: status.indicator,
          last_updated: page.updated_at,
          message: status.description,
        });
      })
      .catch(error => {
        console.error('Error fetching GitHub status:', error);
      });
    
  }, []);

  return (
    <>
      <InfoCard title="GitHub Status">
        {statusData ? (
          <Box>
            <Typography>Status: {statusData.status}</Typography>
            <Typography>Last Updated: {statusData.last_updated}</Typography>
          </Box>
        ) : (
          <Typography>Loading GitHub status...</Typography>
        )}
      </InfoCard>
    </>
  );
};
